  .input-container {
    position: relative;
    margin: 20px;
}

input[type="text"] {
    width: 500px;
    padding: 10px;
    border: none;
    border-bottom: 2px solid #ccc;
    outline: none;
    transition: border-bottom-color 0.3s ease;
}

input[type="text"]:focus {
    border-bottom-color: dodgerblue;
}

label {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: #888;
    pointer-events: none;
    transition: all 0.3s ease;
}

.input-focused label,
.input-filled label {
    top: 0;
    font-size: 12px;
    color: black;
    font-weight: bold;
}
