.Main_login{
    display: flex;
    width: 60vw;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 20px rgb(46 36 36 / 30%);
    margin-top: 10%;
    /* add by jagaddesh for invalid login cridentials */
    position:relative
  }
  .coursel{
    width: 45%;
    display:flex;
    align-items: center;
    justify-content: center;
    background-color: #0b0a20e8;
    padding: 30px;
  }
  .Login_body{
    background-color:var(--card) ;
    width: 65%;
    padding:30px;
  }
  .Login_Form {
    width: 300px;
    margin: auto;
  }
