  .required-label {
    color: transparent;
    bottom: 0px;
    position: absolute;
    line-height: 0px;
    display: block;
    left: 0;
    right: 0;
    transform: scale3d(1,1,1);
    transition: 0.2s ease-in-out;
    border-bottom: 2px solid red;
    width: 8%;
    z-index: 1;
  }
  .Login_Form .divFlex {
    margin: 10px 0px ;
  }
  .sign_in_out_buttons {
    display: flex;
    margin: 15px 0px;
    justify-content: space-between;
  }
  .sign_in_out_buttons {
    display: flex;
    margin: 15px 0px;
    justify-content: space-between;
  }
  .Sign_in{
    width: 100%;
    border: none;
    height:40px;
    margin-top: 20px;
    background-image: linear-gradient(to left,#465564, #95bee3);
    border-radius: 30px;
    font-weight: 500;
  }
  .remember_forget input:checked ~ .checkBox {
    background-color: #ccc;
  }
  h2{
    font-size: 18px;
    color: #fff;
    font-size: bold;
    font-weight: 500;
  }

  h5{
    font-size: 18px;
    color: #57d6c5;
  }
  .divFlex label{
    position: absolute;
    top: -5px;
    left: 0;
    cursor: text;
    color: #a5a5a5;
    transform: translateY(18px);
    transition: transform .15s ease-in-out;
    transform-origin: 0% 100%;
    font-size: 12px;
  
  }
  .divFlex input:focus{
    border-bottom: 1px solid var(--basic) !important;
    box-shadow: 0 1px 0 0 #CCC;
    outline: 1px solid transparent;
  }
  .divFlex input[type="text"]:focus ~ label{
    color:var(--basic);
    font-weight: 500;    
    font-size: 10.5px;
    transform: translateY(2px);
  }
  .divFlex input[type="password"]:focus ~ label{
    color:var(--basic);
    font-weight: 500;    
    font-size: 10.5px;
    transform: translateY(2px);
  }
  
  .divFlex input[type="text"]:valid~label {
    font-size: 10.5px;
    transform: translateY(2px);
    font-weight: 600; 
  }

  .divFlex input[type="password"]:valid~label {
    font-size: 10.5px;
    transform: translateY(2px);
    font-weight: 600; 
  }
  
  .divFlex input[type="text"]:disabled {
    padding: 9px 5px 9px 0px !important;
    display: block;
    border: none !important;
    border-bottom: 1.3px dashed #a2a1a1 !important;
    outline: none !important;
    color: #0e0e0e;
    font-size: 12px;
    font-weight: normal;
    color: #000;
    cursor: auto;
    box-shadow: none;
    border-radius: 0;
  }
  .divFlex input[type="password"]:disabled {
    padding: 9px 5px 9px 0px !important;
    display: block;
    border: none !important;
    border-bottom: 1.3px dashed #a2a1a1 !important;
    outline: none !important;
    color: #0e0e0e;
    font-size: 12px;
    font-weight: normal;
    color: #000;
    cursor: auto;
    box-shadow: none;
    border-radius: 0;
  }
  .divFlex input[type="text"]:disabled ~ label{
    font-size: 10.5px;
    transform: translateY(0px);
    color:#696868;
    font-weight: 600; 
  }
  .divFlex input[type="password"]:disabled ~ label{
    font-size: 10.5px;
    transform: translateY(0px);
    color:#696868;
    font-weight: 600; 
  }
  input[type="text"]:disabled {
  cursor: not-allowed !important;
  opacity: 0.7;
  }
  input[type="password"]:disabled {
    cursor: not-allowed !important;
    opacity: 0.7;
  }
  
  .divFlex input[type="password"]:disabled ~ label{
    font-size: 10.5px;
    transform: translateY(0px);
    color:#696868;
    font-weight: 500; 
  }
  input[type="password"]:disabled {
  cursor: not-allowed !important;
  opacity: 0.7;
  }
  
  